import React from 'react'
import PropTypes from 'prop-types'
import { Link } from 'react-router-dom'
import { connect } from 'react-redux'
import Posts from '../../components/Posts'
import Navbar from '../../components/Navbar'
import InfiniteScroll from 'react-infinite-scroller'
import classNames from 'classnames'
import { selectTab, fetchPostsIfNeeded } from '../../actions'
import './home.css'

class Home extends React.Component {
  constructor (props) {
    super(props)
    this.handleChange = this.handleChange.bind(this)
    this.loadMore = this.loadMore.bind(this)
  }

  componentDidMount () {
    const { dispatch, selectedTab, posts } = this.props
    // why this.props undefined
    // 
    if (posts.length === 0) {
      dispatch(fetchPostsIfNeeded(selectedTab, 1))
    }
  }

  componentWillReceiveProps (nextProps) {
    // selectedTab
    //  
    // 
    if (nextProps.selectedTab !== this.props.selectedTab) {
      const { dispatch, selectedTab, posts } = nextProps
      if (posts.length === 0) {
        dispatch(fetchPostsIfNeeded(selectedTab, 1))
      }
      window.scrollTo(0, 0)
    }
  }

  loadMore () {
    // 
    const { dispatch, pageCount, selectedTab, isFetching } = this.props

    if (!isFetching) {
      const nextPageCount = pageCount + 1
      dispatch(fetchPostsIfNeeded(selectedTab, nextPageCount))
    }
  }

  handleChange () {
    const nextTab = this.props.history.location.search.slice(5)
    // this.propsselectedTab
    this.props.dispatch(selectTab(nextTab))
  }

  render () {
    const { posts, isFetching, pageCount, selectedTab } = this.props
    const isEmpty = posts.length === 0

    return (
      <div className='home'>
        <ul className='header-nav' onClick={this.handleChange}>
          <li><Link to='/?tab=all' className={classNames({'selected': selectedTab === 'all'})}></Link></li>
          <li><Link to='/?tab=good' className={classNames({'selected': selectedTab === 'good'})}></Link></li>
          <li><Link to='/?tab=share' className={classNames({'selected': selectedTab === 'share'})}></Link></li>
          <li><Link to='/?tab=ask' className={classNames({'selected': selectedTab === 'ask'})}></Link></li>
          <li><Link to='/?tab=job' className={classNames({'selected': selectedTab === 'job'})}></Link></li>
          <li><Link to='/?tab=dev' className={classNames({'selected': selectedTab === 'dev'})}></Link></li>
        </ul>
        { isEmpty ? (isFetching ? <h2>loading...</h2> : <h2>empty</h2>)
          : <InfiniteScroll
            pageStart={0}
            // loadMore={debounce(this.loadMore.bind(this), 1000)}
            loadMore={this.loadMore}
            hasMore
            loader={<div>load.......</div>}>
            <Posts posts={posts} isFetching={isFetching} key={pageCount + 1} />
          </InfiniteScroll>
        }
        <Navbar />
      </div>
    )
  }
}

Home.propTypes = {
  isFetchings: PropTypes.string,
  dispatch: PropTypes.func.isRequired,
  posts: PropTypes.array.isRequired,
  pageCount: PropTypes.number,
  selectedTab: PropTypes.string.isRequired
}

// stateprops
const mapStateToProps = state => {
  const { selectedTab, postsByTab } = state
  const {
    isFetching,
    lastUpdated,
    pageCount,
    items: posts
  } = postsByTab[selectedTab] || {
    items: []
  }

  return {
    selectedTab,
    posts,
    pageCount,
    isFetching,
    lastUpdated
  }
}

export default connect(mapStateToProps)(Home)



